<script>
import useVuelidate from "@vuelidate/core";
import { required, numeric, email, minLength, maxLength, helpers } from "@vuelidate/validators";
import { defineComponent } from "@vue/composition-api";
import localizeFilter from '@/filters/localize.filter'

const phoneNumber = helpers.regex(
  /^[+0-9]+$/
)

export default defineComponent({
  props: {
    token: {
      type: String,
      default: null
    }
  },
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      email: "",
      // email2: "",
      nickname: "",
      password: "",
      password2: "",
      firstName: "",
      lastName: "",
      telegramName: "",
      age: null,
      phoneNumber: "",
      country: {
        inputValue: "",
        selectValue: null,
        inputDate: undefined,
        validation: null,
        // inputRules: [(value) => value === "Ben" || "Should be Ben"],
        // selectRules: [(value) => value === "Minsk" || "Should be Minsk"],
        // dateRules: [(value) => !!value || "Should be date"],
      },
      sex: {
        inputValue: "",
        selectValue: null,
        inputDate: undefined,
        validation: null,
        options: [{
          text: localizeFilter('male'),
          value: 'male'
        }, {
          text: localizeFilter('female'),
          value: 'female'
        },
        {
          text: localizeFilter('other'),
          value: 'other'
        },

        ]
        // inputRules: [(value) => value === "Ben" || "Should be Ben"],
        // selectRules: [(value) => value === "Minsk" || "Should be Minsk"],
        // dateRules: [(value) => !!value || "Should be date"],
      },
      // middleName: "",
      sponsorEmail: "",
      conditions1Accepted: false,
      conditions2Accepted: true,
    };
  },
  validations() {
    return {
      email: {
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
        email: helpers.withMessage(localizeFilter('field_must_be_valid_email_address'), email)
      },
      // email2: { required, email },
      nickname: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      password: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      password2: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      firstName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      lastName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      telegramName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
      age: { numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric) },
      phoneNumber: {
        phoneNumber: helpers.withMessage(localizeFilter('field_must_be_valid_phone_number'), phoneNumber),
        minLength: helpers.withMessage(localizeFilter('min_length', [8]), minLength(8)),
        maxLength: helpers.withMessage(localizeFilter('min_length', [18]), maxLength(18)),
      },
      // middleName: { required },
      sponsorEmail: {
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
        email: helpers.withMessage(localizeFilter('field_must_be_valid_email_address'), email)
      }
    };
  },
  methods: {
    openBot() {
        // window.open('')
    },
    acceptСonditions1() {
        this.conditions1Accepted = !this.conditions1Accepted
    },
    acceptСonditions2() {
        this.conditions2Accepted = !this.conditions2Accepted
    },
    async submitHandler() {
      console.log(this.sex.selectValue, this.country.selectValue)
      this.v$.$touch();
      this.$refs.form.validate();
      if (this.v$.$error) {
        console.log(this.v$.$error)
        return;
      }
      if (!this.conditions1Accepted || !this.conditions2Accepted) { return }
      if(!this.sex.selectValue || !this.country.selectValue) { return }
      // if (this.email != this.email2) {
      //   this.$error({
      //     message: localizeFilter('email_not_match')
      //   })
      //   return 
      // }
      if (this.password != this.password2) {
        this.$error({
          message: localizeFilter('passwords_not_match')
        })
        return 
      }
      const formData = {
        email: this.email, 
        firstName: this.firstName, 
        nickname: this.nickname, 
        lastName: this.lastName, 
        telegramName: this.telegramName,
        age: this.age || null,
        phoneNumber: this.phoneNumber || null,
        country: this.country.selectValue.value,
        sex: this.sex.selectValue.value,
        sponsorEmail:this.sponsorEmail, 
        password: this.password}
      try {
        await this.$store.dispatch('register', formData)
        await this.$store.dispatch('login', formData)
        const {scope, email} = await this.$store.dispatch('getProfile')
        if (scope=='not_activated_client') {
          this.$router.push('/confirmEmail?email='+email)
        } else {
          this.$router.push('/cabinet')
        }
        this.$message(localizeFilter('registration_successfull'))
      } catch (e) {
          this.$error(e)
      }
    },
  },
  computed: {
    countries() {
      return this.$store.getters.localizedCountries;
    },
  },
  async mounted() {
    const token = this.token || localStorage.referralToken
    try {
      if(token){
        const data = await this.$store.dispatch('getEmailByReferralToken',{token})
        this.sponsorEmail = data.email
      } else {
        this.sponsorEmail = 'official@dreamoflives.com'
      }
        document.getElementById('nickname').focus()
      } catch (e) {
          this.$error(e)
      }
  }
});
</script>

